.Social-Item {
  margin-left: 14px;
  width: 25px;
  height: 25px;
  background-repeat: no-repeat;
  background-size: 80%;
  background-position: center;
  background-color: #b4b4b4;
  border: 4px solid transparent;
  border-radius: 50%;
  cursor: pointer;
  -webkit-transition: opacity 0.25s ease;
  transition: opacity 0.25s ease;
}

.Social-Item:first-of-type {
  margin-left: 0;
}

.Social-Item:hover {
  opacity: 0.75;
}

.Social-Item_type_twitter {
  background-image: url(../../static/media/twitter.ebd351ee.svg);
}

.Social-Item_type_facebook {
  background-image: url(../../static/media/facebook.27f2d9a9.svg);
}

.Social-Item_type_unknown {
  background-image: url(../../static/media/unknown.896ffa9e.svg);
}

.Social-Item_type_google {
  background-image: url(../../static/media/google-plus.99cd063d.svg);
}

.Social-Item_type_youtube {
  background-image: url(../../static/media/youtube.949cb85b.svg);
}
