.Menu {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.Menu-Item {
  color: #ffffff;
  font-size: 22px;
  font-weight: 700;
  line-height: 22px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
