.Slider {
  margin: 0 auto;
  height: 593px;
  position: relative;
  white-space: nowrap;
  overflow: hidden;
}

.Slider-Wrapper {
  position: relative;
  height: 100%;
  width: 100%;
}

@media screen and (max-width: 600px) {
  .Slider {
    height: 50vh;
  }
}

.Slider-Item {
  display: inline-block;
  height: 100%;
  width: 100%;
}

.Slider-Arrow {
  width: 57px;
  height: 57px;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  cursor: pointer;
  background-image: url(../../static/media/arrow.0a23b520.svg);
  background-size: 100%;
  background-repeat: no-repeat;
  z-index: 2;
  -webkit-transition: opacity 0.25s ease;
  transition: opacity 0.25s ease;
}

.Slider-Arrow:hover {
  opacity: 0.75;
}

.Slider-Arrow_type_left {
  left: 80px;
}

.Slider-Arrow_type_right {
  right: 80px;
  -webkit-transform: translateY(-50%) rotate(180deg);
          transform: translateY(-50%) rotate(180deg)
}

@media screen and (max-width: 800px) {
  .Slider-Arrow_type_left {
    left: 35px;
  }

  .Slider-Arrow_type_right {
    right: 35px;
  }
}
